.App {
  text-align: center;
}
.App >img{
  width: 100px;
  height:100px;
  margin-top: 2rem;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app__form{
   padding: 20px;
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #e9e9eb;
    z-index: 1;
}

.app__formControl{
  display: flex !important;
  flex-direction: row !important;
  
}

.app__Input{
  flex: 0.94;
}
.app__iconButton{
  flex:0;
}

